.lozung {
    color: white;
    font-size: 25px;
    padding-top: 125px;
}
.col { border: 1px solid transparent; }

.border-1px-black {
    border: 1px solid black;
}
.text-about {
    font-style: normal;
    font-variant: normal;
    font-weight: normal;
    font-size: 14px;

    line-height: 1.3em;
    font-family: din-next-w01-light,din-next-w02-light,din-next-w10-light,sans-serif;
    text-align: justify;
}
.txtNew {
    word-wrap: break-word;
    text-align: start;
}
.txtNew_override-left * {
    text-align: left !important;
}
.txtNew_override-right * {
    text-align: right !important;
}
.txtNew_override-center * {
    text-align: center !important;
}
.txtNew_override-justify * {
    text-align: justify !important;
}
.txtNew > * {
    pointer-events: auto;
}
.txtNew li {
    font-style: inherit;
    font-weight: inherit;
    line-height: inherit;
    letter-spacing: normal;
}
.txtNew ol,
.txtNew ul {
    padding-left: 1.3em;
    padding-right: 0;
    margin-left: 0.5em;
    margin-right: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew ul {
    list-style-type: disc;
}
.txtNew ol {
    list-style-type: decimal;
}
.txtNew ol ul,
.txtNew ul ul {
    list-style-type: circle;
}
.txtNew ol ul ul,
.txtNew ul ul ul {
    list-style-type: square;
}
.txtNew ol ol ul,
.txtNew ul ol ul {
    list-style-type: square;
}
.txtNew ol[dir="rtl"],
.txtNew ul[dir="rtl"] {
    padding-left: 0;
    padding-right: 1.3em;
    margin-left: 0;
    margin-right: 0.5em;
}
.txtNew ol[dir="rtl"] ol,
.txtNew ol[dir="rtl"] ul,
.txtNew ul[dir="rtl"] ol,
.txtNew ul[dir="rtl"] ul {
    padding-left: 0;
    padding-right: 1.3em;
    margin-left: 0;
    margin-right: 0.5em;
}
.txtNew p {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew h1 {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew h2 {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew h3 {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew h4 {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew h5 {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew h6 {
    margin: 0;
    line-height: normal;
    letter-spacing: normal;
}
.txtNew a {
    color: inherit;
}


